<template>
	<view :class="{body:models=='1'||models=='', bodyNight:models=='0'}">
		<!-- 删除模态窗 -->
		<u-modal :show="show" content="确定要删除做题记录吗?将无法恢复哟!" :showCancelButton='true' :closeOnClickOverlay="true" @close="this.show=false" @cancel="this.show=false" @confirm="resets"></u-modal>
		<img v-if="models=='1'" class="backgroundImg" src="../../static/index/reportBg.png">
		<img v-else class="backgroundImg" src="../../static/index/reportBg_night.png">
		<view class="head">
			<img :src="avatarUrl || '../../static/index/user_head.png'">
			<view class="text">
				<view class="name">{{ nickName || '同学' }}</view>
				<view class="tips">恭喜你!完成本次练习</view>
			</view>
		</view>
		<view class="content">
			<view class="statistics">
				<view class="cell">
					<view class="title">做对题数</view>
					<view class="number"><view class="">{{data.correct_num}}</view>题</view>
				</view>
				<view class="cell">
					<view class="title">总共得分</view>
					<view class="number"><view class="">{{data.total_score}}</view>分</view>
				</view>
				<view class="cell">
					<view class="title">平均分</view>
					<view class="number"><view class="">{{Math.floor(data.avg_score * 100)/100}}</view>分</view>
				</view>
			</view>
			<view class="tipTitle">
				<view class="title">答题卡<view class="tip">(点击题号可查看对应解析)</view></view>
			</view>
			<view class="answer">
				<view class="legend">
					<view class="tureLegend">正确</view>
					<view class="falseLegend">错误</view>
				</view>
				<view class="answerList">
					<view class="cell" v-for="(item,index) in data.card" :key="index">
						<view :class="{trueAswerCell:item.question_state==1, falseAswerCell:item.question_state==0}" @click="toDetial(index)">{{index+1}}</view>
					</view>
				</view>
			</view>
			<view class="buttons">
				<button class="lookAnswer" @click="backToIndex">完成</button>
				<button class="reset" @click="this.show=true">重置(重做)</button>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { report, reset } from '@/common/http.api.js';
	export default {
		data() {
			return {
				models:'1', //白天模式
				optionType:'', //入口类型
				token:'',
				unitId:'',
				avatarUrl:'',
				nickName:'',
				show:false,
				title:'',
				isTrue:'',
				data:{}
			}
		},
		onLoad(option) {
			this.optionType = option.type
			this.models = store.state.model
			this.token = store.state.vuex_token
			this.isTrue = option.isTrue
			if(this.models=='0'){
				console.log('进入夜间模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#011733',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}else{
				console.log('进入白天模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#75b9ff',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}
			this.avatarUrl = store.state.vuex_user.avatarUrl
			this.nickName = store.state.vuex_user.nickName
			this.unitId = option.unitId
			this.title = option.title
			report({token:this.token, unit_id:this.unitId}).then(res=>{
				console.log(res)
				this.data = res.data.data
				if(res.data.code==-1){
					this.$u.toast(res.data.data)
				}
			}).catch(err=>{
				console.log(err)
			})
		},
		methods:{
			toDetial(e) {
				uni.navigateTo({
					url:'/pages/index/Details?unit_it='+this.unitId+"&type="+this.optionType+"&title="+this.title+"&answerCard=1"+'&current='+e+'&wrong=0'
				})
			},
			// 回到相应的主页面
			backToIndex() {
				// 返回首页
				uni.switchTab({
					url:'/pages/index/index'
				})
				// 判断真题进入还是模拟题集进入
				/* if(this.isTrue==1){
					// 返回真题
					uni.reLaunch({
						url: '/pages/index/trueQuestions?enter='+0 
					})
				}else if(this.isTrue==0){
					// 返回模拟题集
					uni.reLaunch({
						url: '/pages/index/simulatedQuestionSet?enter='+0 
					})
				}else{
					// 返回考点刷题
					uni.reLaunch({
						url: '/pages/index/testSiteBrushQuestions?enter='+0 
					})
				} */
			},
			// 重置
			resets() {
				this.show = false
				reset({token:this.token, unit_id:this.unitId}).then(res=>{
					console.log(res)
					this.$u.toast("重置成功,即将返回第一题")
					setTimeout(()=>{
						uni.reLaunch({
							url:'/pages/index/Details?unit_it='+this.unitId+"&type="+1+"&title="+this.title+"&answerCard=1"+'&current='+0+'&wrong=0'
						})
					},1000)
				}).catch(err=>{
					console.log(err)
				})
				console.log("重置成功")
			}
		}
	}
</script>

<style lang="scss">
	.backgroundImg{
		width: 100vw;
		height: 200rpx;
		position: absolute;
		top: 0;
		left: 0;
	}
	.head{
		position: absolute;
		top: 20rpx;
		left: 30rpx;
		right: 30rpx;
		display: flex;
		align-items: center;
		img{
			width: 120rpx;
			height: 120rpx;
			border-radius: 50%;
			margin-right: 30rpx;
		}
		.name{
			color: #fff;
			font-size: 28rpx;
			margin-bottom: 10rpx;
		}
		.tips{
			background-color: #fff;
			padding: 4rpx 20rpx;
			border-radius: 50rpx;
			color: #2f83da;
			font-size: 28rpx;
		}
	}
	.content{
		width: 90%;
		position: absolute;
		top: 160rpx;
		left: 5%;
		right: 5%;
		.statistics{
			background-color: #fff;
			border-radius: 20rpx;
			display: flex;
			justify-content: space-around;
			padding: 30rpx 0;
			margin-bottom: 20rpx;
			.cell{
				text-align: center;
				font-size: 24rpx;
				.title{
					color: #2f83da;
					margin-bottom: 10rpx;
				}
				.number{
					view{
						display: inline-block;
						font-size: 32rpx;
					}
				}
			}
		}
		.tipTitle{
			margin-bottom: 10rpx;
			.title{
				display: flex;
				align-items: center;
				.tip{
					margin-left: 10rpx;
					font-size: 22rpx;
					color: #77c2ff;
				}
			}
		}
		.answer{
			padding: 20rpx;
			background-color: #fff;
			border-radius: 20rpx;
			.legend{
				font-size: 24rpx;
				display: flex;
				margin-bottom: 30rpx;
				margin-left: 4%;
				.tureLegend::before{
					content: '';
					display: inline-block;
					background-color: #50d891;
					width: 16rpx;
					height: 16rpx;
					border-radius: 50%;
					margin-right: 10rpx;
				}
				.tureLegend{
					display: flex;
					align-items: center;
					margin-right: 20rpx;
				}
				.falseLegend::before{
					content: '';
					display: inline-block;
					background-color: #ff6227;
					width: 16rpx;
					height: 16rpx;
					border-radius: 50%;
					margin-right: 10rpx;
				}
				.falseLegend{
					display: flex;
					align-items: center;
				}
			}
			.answerList{
				display: flex;
				flex-wrap: wrap;
				.cell{
					.trueAswerCell{
						background-color: #50d891;
					}
					.falseAswerCell{
						background-color: #ff6227;
					}
					width: 20%;
					display: flex;
					justify-content: center;
					margin-bottom: 30rpx;
					view{
						text-align: center;
						width: 80rpx;
            font-size: 28rpx;
						line-height: 80rpx;
						color: #fff;
						height: 80rpx;
						border-radius: 50%;
					}
				}
			}
		}
		.buttons{
			position: fixed;
			width: 90%;
			left: 5%;
			padding: 20rpx 0px;
			right: 5%;
			background-color: #f7f7fb;
			bottom: 0;
			display: flex;
			justify-content: space-between;
			.lookAnswer{
				width: 34%;
				background-color: #6ab4ff;
				color: #fff;
				font-size: 28rpx;
				line-height: 88rpx;
				border-radius: 100rpx;
			}
			.reset{
				width: 34%;
				background-color: #ff8d8a;
				color: #fff;
				font-size: 28rpx;
				line-height: 88rpx;
				border-radius: 100rpx;
			}
		}
	}
	.body{
		min-height: 100vh;
		padding-bottom: 140rpx;
		background-color: #f7f7fb;
		position: relative;
		.content{
			.statistics{
				background-color: #fff;
				.cell{
					.title{
						color: #2f83da;
					}
				}
			}
			.tipTitle{
				.title{
					.tip{
						color: #77c2ff;
					}
				}
			}
			.answer{
				background-color: #fff;
				.answerList{
					.cell{
						view{
							color: #fff;
						}
					}
				}
			}
			.buttons{
				.lookAnswer{
					background-color: #6ab4ff;
					color: #fff;
				}
				.reset{
					background-color: #ff8d8a;
					color: #fff;
				}
			}
		}
	}
	.bodyNight{
		min-height: 100vh;
		padding-bottom: 140rpx;
		position: relative;
		background-color: #3b3b3b;
		.content{
			.statistics{
				background-color: #303030;
				.cell{
					.title{
						color: #fff;
					}
					.number{
						color: #fff;
					}
				}
			}
			.tipTitle{
				color: #fff;
				.title{
					.tip{
						color: #eee;
						font-weight: 100;
					}
				}
			}
			.answer{
				background-color: #303030;
				.legend{
					color: #fff;
				}
				.answerList{
					.cell{
						view{
							color: #fff;
						}
					}
				}
			}
			.buttons{
				background-color: #3b3b3b;
				.lookAnswer{
					background-color: #6ab4ff;
					color: #fff;
				}
				.reset{
					background-color: #ff8d8a;
					color: #fff;
				}
			}
		}
	}
</style>